body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.wrapper {
    padding-top: 50px;
    width: 100%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    padding: 20px;
  }

.container-fluid {
    height: 100%;
    overflow: auto;
}

.navbar {
    position: fixed;
    margin: 0;
    top: 0;
    width: 100%;
    height: 70px;
    z-index: 2;
}

.jumbotron {
    background-image: url(/ClickMemoryGame/static/media/wallpaper.b977a1da.jpg);
    opacity: 0.5;
    min-height: 400px;
    padding: 180px 20px;
    padding-bottom: 140px;
    text-align: center;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: -1;
}

.container {
    color: black;
    text-align: center;
    position: absolute;
    top: 150px;
    left: 150px;
}

.display-4 {
    font-size: 45px;
    font-weight: bold;
    margin: 0;
}

.lead {
    font-weight: bold;
}

.card-container {
    overflow: auto;
}

.guessResponse {
    color: white;
    z-index: 3;
    position: absolute;
    left: 850px;
}

.score {
    color: white;
    position: absolute;
    right: 40px;
}



.title{
    color: white;
    font-size: 40px;
    position: fixed;
    left: 50px;
    top: 5px;
    z-index: 3;
    
}
.card {
    position: relative;
    top: 500px;
    background: #fff;
    border-radius: 3px;
    border: 5px solid #bee0ea ;
    margin: 1rem;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: center;
}

.card > .img-container {
  overflow: auto;
}

.card > .img-container > .click-item > img {
    width: 170px;
    height: 170px;
}

.card:hover {
    box-shadow: 0 3px 10px #999, 0 3px 10px #999;
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}




 .score {
    color: white;
    position: fixed;
    right: 5px;
    top: 15px;
    z-index: 3;
} 

.body {
    -webkit-justify-content: center;
            justify-content: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin-left: 250px;
    min-height: 80%;
    width: 60%;
}

